import "./styles.css";
import { useState } from "react";
const create = require("crypto").createHmac;

export default function App() {
  const [clientId, setClientId] = useState("client_id");
  const [clientSecret, setClientSecret] = useState("client_secret");
  const [userInfo, setUserInfo] = useState(
    JSON.stringify({
      first_name: "firstname",
      last_name: "lastname",
      email: "someemail2@example.com",
      external_id: "asdfasdf",
      bio: "This is the users bio",
      company: "twine",
      title: "the title",
      linkedin_url: "linkedin_url",
      photo_url:
        "https://arc-anglerfish-washpost-prod-washpost.s3.amazonaws.com/public/HUINGM2XDY4IBM3OLULIRQH2FI.jpg"
    })
  );

  const signature = create("sha256", clientSecret)
    .update(userInfo)
    .digest("hex");

  const curlCommand = `curl --request POST \\
   --url https://stageapi.thegathering.co/auth/otp \\
   --header 'Accept: application/json' \\
   --header 'Authorization: HMAC-SHA256 ${clientId},${signature}' \\
   --header 'Content-Type: application/json' \\
   --data '${userInfo}'`;

  const updateClientId = (event) => {
    setClientId(event.target.value);
  };

  const updateClientSecret = (event) => {
    setClientSecret(event.target.value);
  };

  const updateData = (event) => {
    setUserInfo(event.target.value);
  };

  return (
    <div className="App">
      <h2>Run this command to get token</h2>
      CLIENT_ID:&nbsp;&nbsp;&nbsp;
      <input
        name="client_id"
        placeholder="client_id"
        id="client_id"
        value={clientId}
        onChange={updateClientId}
      />
      <br />
      CLIENT_SECRET:&nbsp;&nbsp;&nbsp;
      <input
        name="client_secret"
        placeholder="client_secret"
        id="client_secret"
        value={clientSecret}
        onChange={updateClientSecret}
      />
      <br />
      <h3>User Data</h3>
      <textarea
        style={{ width: "90%" }}
        rows="10"
        onChange={updateData}
        value={userInfo}
      />
      <h3>Run this curl command to generate token</h3>
      <textarea style={{ width: "90%" }} rows="10" value={curlCommand} />
    </div>
  );
}
